:root {
  --spacing-unit: 8px;
  --spacing-xxs: calc(var(--spacing-unit) * 0.375);
  --spacing-xs: calc(var(--spacing-unit) * 0.5);
  --spacing-sm: calc(var(--spacing-unit) * 2);
  --spacing-md: calc(var(--spacing-unit) * 4);
  --spacing-lg: calc(var(--spacing-unit) * 8);
  --spacing-xl: calc(var(--spacing-unit) * 16); }

:root {
  --color-primary-h: 47;
  --color-primary-s: 100%;
  --color-primary-l: 58%;
  --color-primary: hsl(var(--color-primary-h), var(--color-primary-s), var(--color-primary-l));
  --color-secondary-h: 36;
  --color-secondary-s: 46%;
  --color-secondary-l: 17%;
  --color-secondary: hsl(var(--color-secondary-h), var(--color-secondary-s), var(--color-secondary-l));
  --color-black: hsl(240, 7%, 12%);
  --color-white: hsl(0, 0%, 100%);
  --color-success-h: 120;
  --color-success-s: 100%;
  --color-success-l: 50%;
  --color-success: hsl(var(--color-success-h), var(--color-success-s), var(--color-success-l));
  --color-warning-h: 28;
  --color-warning-s: 100%;
  --color-warning-l: 55%;
  --color-warning: hsl(var(--color-warning-h), var(--color-warning-s), var(--color-warning-l));
  --color-error-h: 3;
  --color-error-s: 100%;
  --color-error-l: 61%;
  --color-error: hsl(var(--color-error-h), var(--color-error-s), var(--color-error-l));
  --color-contrast-lower: hsl(0, 0%, 95%);
  --color-contrast-low: hsl(240, 1%, 83%);
  --color-contrast-medium: hsl(240, 1%, 48%);
  --color-contrast-high: hsl(240, 4%, 20%);
  --color-contrast-higher: hsl(240, 8%, 12%); }

[data-theme="dark"] {
  --color-contrast-lower: hsl(0, 0%, 16%);
  --color-contrast-low: hsl(240, 4%, 20%);
  --color-contrast-medium: hsl(240, 1%, 48%);
  --color-contrast-high: hsl(240, 1%, 83%);
  --color-contrast-higher: hsl(0, 0%, 95%); }

:root {
  --radius: 0.25em;
  --radius-sm: calc(var(--radius) / 2);
  --radius-md: var(--radius);
  --radius-lg: calc(var(--radius) * 2); }

:root {
  --sidebar-width: 280px;
  --container-max-width: 1920px; }
